@import '../../theme.scss';

.feedback_inner {
  height: 100%;
  .feedback_left {
    height: 100%;
    display: flex;
    flex-direction: column;

    .feedback_l_head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px;
      font-size: 18px;
      font-weight: 500;
      border-bottom: 1px solid #d9d9d9;

      i {
        cursor: pointer;
      }
    }

    .feedback_l_body {
      flex: 1;
      background-color: #fff;
      overflow-y: auto;

      .l_listcell {
        padding: 10px;
        cursor: pointer;

        .subject {
          font-size: 16px;
          color: rgba(0, 0, 0, 1);
          margin-bottom: 5px;
        }

        .message {
          height: 36px;
          line-height: 18px;
          font-size: 12px;
          color: rgba(2, 2, 2, 1);
          overflow: hidden;
          text-overflow: ellipsis;
          -webkit-line-clamp: 2;
          word-break: break-all;
          display: -webkit-box;
          -webkit-box-orient: vertical;
        }

        .created {
          margin-top: 5px;
          color: rgba(128, 128, 128, 1);
          font-size: 10px;
          text-align: right;
        }

        &:hover {
          background-color: $hover;
        }
      }

      .l_active {
        background-color: $hover;
      }
    }
  }

  .feedback_right {
    height: 100%;
    border-left: 1px solid #D3D3D3;

    .feedback_right_inner {
      display: flex;
      height: 100%;
      flex-direction: column;

      .feedback_r_head {
        padding: 10px 10px 0 10px;
        background-color: #fff;
        border-bottom: 1px solid #d9d9d9;

        .r_head_title {
          font-size: 18px;
          font-weight: 500;
          color: rgba(0, 0, 0, 0.8);
        }

        .r_head_bottom {
          display: flex;
          justify-content: space-between;
          color: rgba(128, 128, 128, 1);
          font-size: 10px;
          text-align: right;
          margin: 10px 0;
        }
      }

      .r_bottom_body {
        flex: 1;
        overflow-y: auto;
        padding-bottom: 40px;

        .detailList {
          background-color: #fff;
          margin: 10px;
          padding: 10px;

          .list {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 12px;
            margin-bottom: 10px;
          }

          .attach {
            margin-top: 10px;

            ul {
              overflow: hidden;
            }

            li {
              float: left;
              width: 50px;
              height: 70px;
              background-color: #d9d9d9;
              display: flex;
              align-items: center;
              margin-right: 10px;
              cursor: pointer;

              img {
                width: 50px;
                max-height: 70px;
              }
            }
          }
        }
      }

      .bottom_btn {
        height: 40px;
        background-color: $bg-color;
        cursor: pointer;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;

        i {
          margin-right: 10px;
        }
      }
    }
  }

}

.fb_block {
  margin-bottom: 10px;

  .fb_lable {
    float: left;
    width: 70px;
    padding-right: 10px;
    box-sizing: content-box;

    .fb_tips {
      color: red;
    }

    .fb_title_tips {
      line-height: 30px;
    }
  }

  .fb_lable1 {
    margin-bottom: 10px;
  }

  .fb_textarea {
    width: 100%;
    float: left;

    textarea,
    input {
      width: 100%;
      padding: 4px;
      color: #666;
      border: 1px solid #ccc;
    }

    textarea {
      height: 150px;
      background-color: transparent;
      position: relative;
      vertical-align: top;
      resize: none;
      overflow: auto;

      &:focus {
        outline: none;
      }
    }

    input {
      height: 30px;
    }
  }
}

